body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.app-container {
  display: flex;
  flex-wrap: wrap;
  background-size: cover;
  justify-content: space-between;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.wrapper {
  display: flex;
  align-items: center;
  margin: 5px;
  padding: 20px;
  border-radius: 5px;
  background-color: rgba(0,0,0,.5);
}

.map-block {
  width: 26.5%;
  flex-direction: column;
}
@media (max-width: 1054px) {
  .map-block {
    width: 100%;
    flex-direction: column;
  }
  .weather-block {
    width: 100% !important;
    padding: 0;
  }
}
@media (max-width: 321px) {
  .map-block {
    padding: 0;
    position: relative;
    left: -5px;
  }
  .weather-next-day {
    flex-direction: column;
  }
}
.ymaps-2-1-76-map {
  -webkit-clip-path: polygon(
    0 0,
    100% 0%,
    100% 75%,
    100% 75%,
    100% 100%,
    89% 88%,
    0 88%
  );
  clip-path: polygon(
    0 0,
    100% 0%,
    100% 75%,
    100% 75%,
    100% 100%,
    89% 88%,
    0 88%
  );
}
.map-position {
  position: relative;
  left: 93px;
  margin: 0;
}

.controls-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
}
.controls-btn {
  flex-grow: 1;
  flex-shrink: 1;
  flex-basis: 0%;
  justify-content: flex-start;
}
.control-search {
  width: 39%;
}
@media (max-width: 1024px) {
  .control-search {
    width: 100%;
  }
}

.MuiOutlinedInput-notchedOutline {
  border-color: white !important;
}
.MuiInputBase-formControl {
  color: white !important;
}
.mic-icon {
  width: 54px;
  height: 54px;
  border: 1px solid #fff;
  text-align: center;
  line-height: 69px;
  /* margin-left: -10px; */
  border-radius: 5px;
  cursor: pointer;
}
.btn-search {
  height: 56px;
  border: 1px solid rgba(255, 255, 255, 0.5) !important;
  color: white !important;
}
.search-panel {
  width: 80%;
}

span {
  color: white;
}
.weather-today {
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
}
.wrapper {
  justify-content: center;
}
.date-app {
  text-align: center;
  border: 2px solid #ccc;
  border-radius: 10px;
  padding: 10px;
}
.weather-block {
  display: flex;
  flex-direction: column;
  width: 64%;
  height: 75vh;
}
.weather-next-day-item {
  display: flex;
  flex-direction: column;
  text-align: center;
  border: 2px solid #ccc;
  border-radius: 10px;
  width: 220px;
  margin: 5px;
}
.weather-next-day {
  display: flex;
}
.weather-about {
  text-align: center;
  margin: 10px;
  display: flex;
}
.weather-today-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.weather-today-icon {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.weather-today-icon h2 {
  font-size: 70px;
}
@media (max-width: 321px) {
  .weather-today-icon {
    display: none;
  }
}
.weather-today-icon img {
  position: relative;
  width: 122px;
  top: 36px;
  right: 119px;
}

